import React from 'react';

const TodoItem = ({ todo, vt }) => {
  const { name, labelArr, priority, memberIDArr, status } = todo;

  const _priority = priority => {
    switch (priority) {
      case 1:
        return <span style={{ color: '#13c2c2' }}>Thấp</span>;
      case 2:
        return <span style={{ color: '#389E0D' }}>Trung bình</span>;
      case 3:
        return <span style={{ color: '#CF1322' }}>Cao</span>;
      default:
        return '';
    }
  };

  const _color = label => {
    switch (label) {
      case 'Frontend':
        return '#389E0D';
      case 'Backend':
        return '#722ed1';
      case 'API':
        return '#13c2c2';
      case 'Issue':
        return '#CF1322';
      default:
        return '#ffffff';
    }
  };

  const _labelArr = labelArr => {
    return labelArr.map(label => (
      <i className='fa fa-circle' style={{ color: _color(label) }}></i>
    ));
  };
  const _member = name => {
    switch (name) {
      case 'user_1':
        return './img/user_1.jpg';
      case 'user_2':
        return './img/user_2.jpg';
      case 'user_3':
        return './img/user_3.jpg';
      case 'user_4':
        return './img/user_14.jpg';
      default:
        return './img/user_5.jpg';
    }
  };

  const _memberIDArr = memberIDArr => {
    return memberIDArr.map(member => (
      <img src={_member(member)} className='user' alt='' />
    ));
  };

  return (
    <tr>
      <td className='text-center'>{vt + 1}</td>
      <td className='text-center'>{name}</td>
      <td className='text-center'>{_labelArr(labelArr)}</td>
      <td className='text-danger font-weight-bold text-center'>
        {_priority(priority)}
      </td>
      <td className='text-center'>{_memberIDArr(memberIDArr)}</td>
      <td className='text-center'>
        <button type='button' className='btn btn-outline-warning'>
          Sửa
        </button>
        <button type='button' className='btn btn-outline-danger'>
          Xóa
        </button>
      </td>
      <td className='text-center'>
        <select className='form-control' readOnly value={status}>
          <option value={1}>Mới</option>
          <option value={2}>Đang thực hiện</option>
          <option value={3}>Hoàn thành</option>
          <option value={4}>Huỷ bỏ</option>
        </select>
      </td>
    </tr>
  );
};

export default TodoItem;
