import React from 'react';

import Filter1 from './left/Filter1';
import Filter2 from './left/Filter2';
import Filter3 from './left/Filter3';
import Filter4 from './left/Filter4';

const Left = () => {
  return (
    <div className='col-md-3 text-center px-0'>
      <div className='header header--left d-flex align-items-center'>
        <img src='./img/user_1.jpg' className='ml-2 user' alt='' />
        <h3 className='text-white d-inline font-weight-light ml-2'>
          Lê Quang Song
        </h3>
      </div>
      <button
        type='button'
        className='btn my-3 btn--newTask'
        data-toggle='modal'
        data-target='#modalTask'
      >
        <i className='fa fa-pencil-square-o' />
        Tạo Task mới
      </button>
      {/* Filter */}
      <div className='px-3'>
        <Filter1 />
        <Filter2 />
        <Filter3 />
        <Filter4 />
      </div>
    </div>
  );
};

export default Left;
